/* universal reset */
* {
  font-family: 'Poppins', sans-serif;
  /* use zero margin and padding instead of browser defaults */
  margin: 0;
  padding: 0;
  /* set box width and height to element's content + padding + border instead of CSS' default box model sizing in which
     box width and height are related only to the element's content because the former is more intuitive
     See https://css-tricks.com/almanac/properties/b/box-sizing/ */
  box-sizing: border-box;
}
/* reset box sizing for pseudo elements */
*::before,
*::after {
  box-sizing: inherit;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

a{
  text-decoration: none;
}

a:visited{
  color:inherit;
}